<template>
  <div class="agency">
    <div>
      <section class="mealList">
        <Tag v-for="(item,key) in data" :key="key" :name="key" closable @on-close="handleCloseMeal(key,item)" type="border" @click.native="seeMealInfo(item)"><span class="meal_tag">app数量：{{item.app_num}}</span></Tag>
      </section>
      <Modal v-model="mealForm" title="升级套餐" width="650">
        <form>
          <div class="agency_div">
            <span class="agency_span"> <font class="font">*</font> 数量 ：</span>
            <input type="text" placeholder="请输入数量" v-model="num" />
          </div>
          <div class="agency_div">
            <span class="agency_span"> <font class="font">*</font>价格：</span>
            <input type="text" placeholder="请输入数量" v-model="price" />
          </div>
          <p class="btn">
            <a href="javascript:void(0)" class="save" @click="saveNum">保存</a>
            <a href="javascript:void(0)" @click="cancel" class="cancel">取消</a>
          </p>
        </form>
        <div slot="footer">
        </div>
      </Modal>
    </div>
    <div class="agency_div">
      <span class="agency_span"> <font class="font">*</font> 数量 ：</span>
      <input type="text" placeholder="请输入数量" v-model="num" />
    </div>
    <div class="agency_div">
      <span class="agency_span"> <font class="font">*</font>价格：</span>
      <input type="text" placeholder="请输入数量" v-model="price" />
    </div>
    <div class="btn">
      <a href="javascript:void(0)" class="save" @click="save">保存</a>
      <a href="javascript:void(0)" @click="cancel" class="cancel">取消</a>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        num: '',
        price: '',
        id: '',
        mealForm: false,
        data: []
      }
    },
    methods: {
      save() {
        this.post("system/App/app", {
          type_auto: this.power,
          type_ssh: 1,
          price: this.price,
          app_num: this.num
        }).then(res => {
          if(res.data.code === 10000) {
            this.$Modal.success({
              title: "设置成功",
              content: res.data.msg
            });
          } else {
            this.$Modal.error({
              title: "设置失败",
              content: res.data.msg
            });
          }
        })
      },
      saveNum(){
        this.post("system/App/update_app", {
          id:this.id,
          price: this.price,
          app_num: this.num
        }).then(res => {
          if(res.data.code === 10000) {
            this.$Modal.success({
              title: "修改成功",
              content: res.data.msg
            });
          } else {
            this.$Modal.error({
              title: "修改失败",
              content: res.data.msg
            });
          }
        })
      },
      cancel() {

      },
      seeMealInfo(item) {
        this.id = item.id;
        this.flag = false;
        this.post("system/App/get_app", {
          id: item.id
        }).then(res => {
          if(res.data.code === 10000) {
            console.log(res.data)
            this.num = res.data.data.app_num
            this.price = res.data.price
          } else {

          }
        })
        this.mealForm = true;
      },
      getUpgade() {
        this.post("system/App/get_app_list", {
        }).then(res => {
          if(res.data.code === 10000) {
            console.log(res.data)
            this.data = res.data.data
          } else {
            this.$Modal.error({
              title: "获取失败",
              content: res.data.msg
            });
          }
        })
      },
      handleCloseMeal(key, item) {
        this.$Modal.confirm({
          title: '是否删除',
          content: '<p>确定要删除该套餐吗</p>',
          onOk: () => {
            this.post("system/App/delete_app", {
              id: item.id,
            }).then(res => {
              if(res.data.code === 10000) {
                this.data.splice(key, 1);
                this.$emit('updateAppMeal', 1, 1)
              } else {
                const content = res.data.msg
                this.$Modal.error({
                  title: "删除失败",
                  content: content
                });
              }
            });
          },
        });
      },
    },
    created() {
      this.power = sessionStorage.getItem('user');
        this.getUpgade()

    },
  }
</script>

<style>
  input {
    display: inline-block;
    padding: 10px 10px;
    border: 1px solid #EAEAEA;
    width: 400px;
    border-radius: 5px;
    background-color: #FFFFFF;
    outline: #6098FF;
  }
  
  .btn {
    padding: 20px 168px;
  }
  
  .save {
    display: inline-block;
    padding: 8px 35px;
    background-color: #6098FF;
    border-radius: 5px;
    color: white;
  }
  
  .agency_span {
    display: inline-block;
    width: 80px;
    font-size: 14px;
  }
  
  .agency_div {
    margin-top: 10px;
    margin-bottom: 20px;
    margin-left: 20px;
  }
  
  .cancel {
    margin-left: 15px;
    display: inline-block;
    padding: 7px 34px;
    border: 1px solid #777777;
    border-radius: 5px;
    color: #777777;
  }
  
  .cancel:hover {
    background: #799EE3;
    color: white;
    text-decoration: none;
    border: none;
  }
  
  .font {
    color: red;
    display: inline-block;
    padding-right: 10px;
  }
  .ivu-tag-border{
    height: 30px;
    line-height: 30px;
  }
</style>


